import api from '../server'
import {mapState} from 'vuex'
import navigate from '../utils/navigate'
export default {
  data(){
    return{
      IMGURL:api.BASEIMGURL,
      defaultImg: 'this.src="' + require('../assets/icon_yizhu@2x.png') + '"',//默认图片
      orderlist:[],
      startpageno:0,
      isScroll:true,
      noMore:false
    }
  },
  computed:{
    ...mapState({
      token:state=>state.token
    })
  },
  watch:{
    token(){
      if(this.$store.state.phone && this.$store.state.token){
        this.getorderlist();
      }else {
        var routername = this.$route.name;
        location.href = '#/login/'+encodeURIComponent(routername)
      }
    }
  },
  created(){
    var _this = this;
    if(this.$store.state.prefrom){//如果不是第一次进来
      if(this.$store.state.phone && this.$store.state.token){
        _this.getorderlist();
      }else {//不是第一次进来如果没有登录跳去登录
        var routername = _this.$route.name;
        location.href = '#/login/'+encodeURIComponent(routername)
      }
    }else {//如果是第一次进来
      if(this.$store.state.phone && this.$store.state.token){
          _this.getorderlist();
      }else if(navigate()=='other' && (!this.$store.state.phone || !this.$store.state.token)){
        var routername = _this.$route.name;
        location.href = '#/login/'+encodeURIComponent(routername)
      }else if(this.$store.state.openid && navigate()=='wechat'){
        _this.getorderlist();
      }
    }
  },
  mounted(){
    const _this = this;
    $(window).scroll(function () {
      if(_this.isScroll){
        var scrollTop = $(this).scrollTop();    //滚动条距离顶部的高度
        var scrollHeight = $(document).height();   //当前页面的总高度
        var clientHeight = $(this).height();    //当前可视的页面高度
        if(scrollTop + clientHeight >= scrollHeight){   //距离顶部+当前高度 >=文档总高度 即代表滑动到底部 count++;
          _this.startpageno++;
          _this.getorderlist();
        }
      }
    })
  },
  methods:{
    getorderlist(){
      var _this = this;
      this.isScroll = false;
      api.getorderlist({startpageno:this.startpageno}).then(function (res) {
        if(res.code === '000'){
          _this.isScroll = true;
          _this.orderlist = _this.orderlist.concat(JSON.parse(res.data));
          if(JSON.parse(res.data)<10){
            _this.isScroll = false;
            _this.noMore = true;
          }
        }else if(res.code === '10007'){
          var routername = _this.$route.name;
          location.href='#/login/'+encodeURIComponent(routername)
        }
      })
    }
  }
}



